import NavBar from "./Components/NavBar";
import "./App.css";
import { useState, useEffect } from "react";
import "./Components/about.css";
import "./Components/Service.css";
import "./Components/test.css";
import "./Components/Contact.css";
import "./Components/footer.css";
import "./Components/hired.css";
import MainBg from "./Components/MainBg";
import About from "./Components/About";
import Services from "./Components/Services";
import Testimonial from "./Components/Testimonial";
import Contact from "./Components/Contact";
import Footer from "./Components/Footer";
import Spinner from "./Components/Spinner";
import Hired from "./Components/Hired";
import Aos from "aos";
import "aos/dist/aos.css";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import ContactWindow from "./Components/ContactWindow";
import Developer from "./Components/Developer";
import SideIcon from "./Components/SideIcon";

function App() {
  //Loading

  const [loading, setLoading] = useState(true);
  useEffect(() => {
    setLoading(true);
    setTimeout(() => {
      setLoading(false);
    }, 2000);
  }, []);

  //Aos js
  useEffect(() => {
    Aos.init({ duration: 1000 });
  }, []);

  const [contact, setContact] = useState("contactWindow-close");

  const handleClose = () => {
    if (contact === "contactWindow rounded-lg shadow hover:shadow-xl") {
      setContact("contactWindow-close");
    } else {
      setContact("contactWindow rounded-lg shadow hover:shadow-xl");
    }
  };
  const handleOpen = () => {
    if (contact === "contactWindow-close") {
      setContact("contactWindow rounded-lg shadow hover:shadow-xl");
    } else {
      setContact("contactWindow-close");
    }
  };

  return (
    <>
      {loading ? (
        <Spinner loading={loading} />
      ) : (
        <Router>
          <NavBar />
          <ContactWindow contact={contact} handleClose={handleClose} />
          <Switch>
            <Route exact path="/">
              <MainBg />
              <About />
              <Services />
              <Testimonial />
              <Contact />
              <Developer />
            </Route>

            <Route exact path="/about">
              {" "}
              <About /> <Developer />{" "}
            </Route>
            <Route exact path="/service" component={Services} />
            <Route exact path="/hired" component={Hired} />
            <Route exact path="/contact" component={Contact} />
          </Switch>
          <SideIcon />
          <Footer handleOpen={handleOpen} />
        </Router>
      )}
    </>
  );
}

export default App;
