import React , {useState , useEffect} from 'react'
import { Link } from 'react-router-dom'
import AnimatedRoute from './AnimatedRoute'
export default function NavBar(props) {

  const [scrolled, setScrolled] = useState(false)
  const [active, setActive] = useState("nav-ul")
    const toggleNav = () => {
      active === "nav-ul"
      ? setActive('nav-ul nav_active')  : setActive('nav-ul');
    }

    const handleScroll = () => {
      const offset = window.scrollY;
      if(offset > 70) {
        setScrolled(true)
      }
      else {
        setScrolled(false)
      }
    }

    useEffect ( () => {
        window.addEventListener('scroll' , handleScroll)
    })

    let navBar = ['nav-bar'];
    let navA = ['nav-a']

    if(scrolled) {
      navBar = ['stick']
      navA = ['nav-a-scrolled']
    };




  	// far fa-window-minimize
  return (
    
    
    <nav className={navBar}>
      <div  className='nav-logo'>  <h1>Weilders In </h1></div>
        <ul className={active}>
            <li> <Link className={navA} to='/'> Home </Link> </li>
            <li id='about'  > <Link className={navA} to='/about'>About us</Link> 
            <div className="drop-down">
              <Link to="/about">Our Developers</Link>
              <Link to="/contact">Know more</Link>
            </div>
            </li>
            <li> <Link className={navA} to='/service'> Our Services</Link> </li>
            <li id='code' > <Link className={navA} to='/hired'> Get Hired</Link> 
            {/* <div className="drop-down2">
              <a href="">Job oriented Courses</a>
              <a href="">Templates and Code</a>
            </div> */}
            </li>
            <li> <Link className={navA} to='/contact'> Contact Us </Link> </li>
        </ul>
       <i  onClick={toggleNav} id='icon-1' className={active === 'nav-ul' ? 'fas fa-bars' : 'fas fa-chevron-right text-light'}></i>
    </nav>
  
  )
}
