import React from 'react'
import mainBg from './Images/illus.jpg'
import AnimatedRoute from './AnimatedRoute'

export default function MainBg() {
  return (
    <AnimatedRoute>
    <div  className='background py-4 mt-3'>
          <div className='content'>
            <h1 className='my-3'>Weilders</h1>
            <p > Weilders are hereby Providing web Services to their clients, and binding their experience with it. </p>
           <button className='  button-explore btn btn-warning text-light pd-5 my-3'> <a className='explore-btn text-light pd-3' href='#contactpage'> Explore more </a></button>          </div>
    <div  className="main-cont">  <img  src={mainBg}  /> </div>
           </div>
    </AnimatedRoute>
  )
}
