import React, {useRef} from "react";
import hiredImg from "./Images/illus.jpg";
import AnimatedRoute from "./AnimatedRoute";
import emailjs from '@emailjs/browser';
import {  toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

export default function Hired() {
  const form = useRef();
  
  const sendEmail = (e) => {
   e.preventDefault();

  

   emailjs.sendForm('service_9z67t1y', 'template_hcx5zus', form.current, '4LvVu58bpkcus2IVY')
   .then(() => {
       const notify = () => toast("Your Resume is uploaded, we will contact you.");
       notify()
     }, () => {
       const notify2 = () => toast("Kuch toh gadbad hai email send nhi hui bro!");
       notify2()
     });
     e.target.reset();
   };
   
  return (
    <AnimatedRoute>
      <div className="hired-box">
        <div className="hired-img">
          <img src={hiredImg} alt="" />
        </div>

        <div className="clip3"></div>
        <div className="clip2"></div>

        <div className=" hired-content flex items-center justify-center p-12">
          <div className="mx-auto w-full max-w-[550px]">
            <form action="https://formbold.com/s/FORM_ID" ref={form}  onSubmit={sendEmail } >
              <div className="mb-3">
                <label
                  htmlFor="name"
                  className="mb-3 block text-base font-medium text-[#07074D]"
                >
                  Full Name
                </label>
                <input
                  type="text"
                  name="user_name"
                  id="name"
                  placeholder="Full Name"
                  className="w-full rounded-md border border-[#e0e0e0] bg-white py-3 px-6 text-base font-medium text-[#6B7280] outline-none focus:border-[#6A64F1] focus:shadow-md"
                />
              </div>
              <div className="mb-3">
                <label
                  htmlFor="email"
                  className="mb-3 block text-base font-medium text-[#07074D]"
                >
                  Email Address
                </label>
                <input
                  type="email"
                  name="user_email"
                  id="email"
                  placeholder="example@domain.com"
                  className="w-full rounded-md border border-[#e0e0e0] bg-white py-3 px-6 text-base font-medium text-[#6B7280] outline-none focus:border-[#6A64F1] focus:shadow-md"
                />
              </div>
              <div className="mb-3">
                <label
                  htmlFor="number"
                  className="mb-3 block text-base font-medium text-[#07074D]"
                >
                  Whatsapp Number
                </label>
                <input
                  type="tel"
                  name="whats_number"
                  id="number"
                  placeholder="Enter your Mobile number"
                  className="w-full rounded-md border border-[#e0e0e0] bg-white py-3 px-6 text-base font-medium text-[#6B7280] outline-none focus:border-[#6A64F1] focus:shadow-md"
                />
              </div>
              <div className="mb-3">
                <label
                  htmlFor="dropCv"
                  className="mb-3 block text-base font-medium text-[#07074D]"
                >
                  Drop Your Cv
                </label>
                <input
                   name="user_resume"
                   type="file"
                    id="dropCv"
                    placeholder="Drop your CV"
                    className="w-full rounded-md border border-[#e0e0e0] bg-white py-3 px-6 text-base font-medium text-[#6B7280] outline-none focus:border-[#6A64F1] focus:shadow-md"
                />
              </div>

              <div>
                <button
                  className="hover:shadow-form rounded-md bg-[#6A64F1] py-3 px-8 text-base font-semibold text-white outline-none"
                  type="submit" value="Send"
                >
                  Submit
                </button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </AnimatedRoute>
  );
}
