import React , {useState , useRef} from 'react'
import emailjs from '@emailjs/browser';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';


export default function ContactWindow( props) {
      const [account, setAccount] = useState({startDate : ""})
    function handleChange(e) {
      const value = e.target.value; 
      setAccount({...account , value})
    }

     //Email Js
     const form = useRef();

     const sendEmail = (e) => {
      e.preventDefault();
  
      emailjs.sendForm('service_9z67t1y', 'template_aahyiwo', form.current, '4LvVu58bpkcus2IVY')
        .then((result) => {
          const notify = () => toast("Thanks for contacting, We will contact you soon");
          notify()
        }, () => {
          const notify2 = () => toast("Kuch toh gadbad hai email send nhi hui bro!");
          notify2()
        });
        e.target.reset();
    };
  
  return (
<> <ToastContainer/>
           <div className={props.contact}>

     <div className="upper">       <h1 >Contact Us</h1>
     <i onClick={props.handleClose} className='fas fa-times' ></i>
</div>
         
         
<div className="flex items-center justify-center p-12">

<div className="mx-auto w-full max-w-[550px]">
  <form action="https://formbold.com/s/FORM_ID" ref={form} onSubmit={sendEmail} >
    <div className="mb-3">
      <label
        htmlFor="name"
        className="mb-3 block text-base font-medium text-[#07074D]"
      >
        Full Name
      </label>
      <input
        type="text"
        id="name"
        name="user_name" 
        required
        placeholder="Full Name"
        className="w-full rounded-md border border-[#e0e0e0] bg-white py-3 px-6 text-base font-medium text-[#6B7280] outline-none focus:border-[#6A64F1] focus:shadow-md"
      />
    </div>
    <div className="mb-3">
      <label
        htmlFor="email"
        className="mb-3 block text-base font-medium text-[#07074D]"
      >
        Email Address
      </label>
      <input
      required
        type="email"
        name="user_email" 
        id="email"
        placeholder="example@domain.com"
        className="w-full rounded-md border border-[#e0e0e0] bg-white py-3 px-6 text-base font-medium text-[#6B7280] outline-none focus:border-[#6A64F1] focus:shadow-md"
      />
    </div>
    <div className="mb-3">
      <label
        htmlFor="subject"
        className="mb-3 block text-base font-medium text-[#07074D]"
      >
        Subject
      </label>
      <input
      required
        type="text"
        name="subject"
        id="subject"
        placeholder="Enter your subject"
        className="w-full rounded-md border border-[#e0e0e0] bg-white py-3 px-6 text-base font-medium text-[#6B7280] outline-none focus:border-[#6A64F1] focus:shadow-md"
      />
    </div>
    <div className="mb-3">
      <label
        htmlFor="date"
        className="mb-3 block text-base font-medium text-[#07074D]"
      >
        Date
      </label>
      <input
      required
        type="date"
        name="date"
        min={new Date().toISOString().split("T")[0]}
        onChange={handleChange}
        
        id="date"
        className="w-full rounded-md border border-[#e0e0e0] bg-white py-3 px-6 text-base font-medium text-[#6B7280] outline-none focus:border-[#6A64F1] focus:shadow-md"
      />
    </div>
    <div className="mb-3">
      <label
        htmlFor="time"
        className="mb-3 block text-base font-medium text-[#07074D]"
      >
        Time
      </label>
      <input
      required
        type="time"
        name="user_time"
        id="time"
        className="w-full rounded-md border border-[#e0e0e0] bg-white py-3 px-6 text-base font-medium text-[#6B7280] outline-none focus:border-[#6A64F1] focus:shadow-md"
      />
    </div>
    <div className="mb-3">
      <label
        htmlFor="message"
        className="mb-3 block text-base font-medium text-[#07074D]"
      >
        Message
      </label>
      <textarea
      required
        rows="4"
        name="message"
        id="message"
        placeholder="Type your message"
        className="w-full resize-none rounded-md border border-[#e0e0e0] bg-white py-3 px-6 text-base font-medium text-[#6B7280] outline-none focus:border-[#6A64F1] focus:shadow-md"
      ></textarea>
    </div>
    <div>
      <button
        className="hover:shadow-form rounded-md bg-[#6A64F1] py-3 px-8 text-base font-semibold text-white outline-none" type='submit' value="Send" 
      >
        Submit
      </button>
    </div>
  </form>
</div>
</div>
<div className="circle1"></div>
<div className="circle2"></div>
</div>
</>
  )
}
